import Service from './Service'
import AuthService from "./AuthService"

class MenuService extends Service {
  constructor () {
    super()
  }

  getDishes (id) {
    return this.request({
      url: 'http://apidonaloladev.us-east-1.elasticbeanstalk.com/api/food-menu/local/' + id,
      method: 'GET',
      body: {
        "idLocal ": id
      }
    })
  }
}

export default new MenuService()
